import React, { FunctionComponent, useEffect } from 'react'
import Gitment from 'gitment'
import 'gitment/style/default.css'

type Props = {
  postTitle: string
}

export const GitComment: FunctionComponent<Props> = (props) => {

  useEffect(() => {
    const gitment = new Gitment({
      id: props.postTitle, // optional
      owner: 'MLuminary',
      repo: 'hutchins.github.io',
      oauth: {
        client_id: '7337a7896083b307f117',
        client_secret: 'ef2a7cbd01054e519a2a05cc84c82ce032434b8a',
      },
      // ...
      // For more available options, check out the documentation below
    })
    // @ts-ignore
    gitment.render('comments')
  })

  return <div id='comments'></div>
}